body{
  font-family: 'Play', sans-serif;
}
.user-stats{
  position: inherit;
  display: block;
  margin: 0;
  color: black;
  padding: 15px 8%;
  font-size: 30px;
}
.profile-title{
  font-size: 40px;
  text-align: center;
  color: red;
  display: block;
  position: inherit;  
  text-decoration: underline;
  margin:0;
}
.profile-page-wrapper{
  background-image:-webkit-gradient(linear, left top, right top, from(rgb(0, 0, 0)), to(rgba(0, 0, 0, 1)));
  background-image:-webkit-linear-gradient(left, rgb(0, 0, 0), rgba(0, 0, 0, 1));
  background-image:-o-linear-gradient(left, rgb(0, 0, 0), rgba(0, 0, 0, 1));
  background-image:linear-gradient(to right, rgb(0, 0, 0), rgba(0, 0, 0, 1));
   /* url(http://themarkconsulting.com/wp-content/uploads/2015/04/Bank_Vault_3D_Wallpaper-HD.jpg); */
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height:150vh;
}
.profile-content-wrapper{
  background-image: -webkit-gradient(linear, left top, right top, from(rgb(253, 255, 101)), to(rgb(255, 250, 96)));
  background-image: -webkit-linear-gradient(left, rgb(253, 255, 101), rgb(255, 250, 96));
  background-image: -o-linear-gradient(left, rgb(253, 255, 101), rgb(255, 250, 96));
  background-image: linear-gradient(to right, rgb(253, 255, 101), rgb(255, 250, 96));
  background-position: center;
  background-size:cover;
  height: 300px;
  width: 60rem;
  border-radius: 4px;
  display: block;
  margin:0 auto; 
  position: relative;
}
.chains{
  background-image: url(./machamp.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 300px;
  width: 60rem;
  display: block;
  margin: 0 auto;
}
.navbar-bottom{
  background: black;
  width: 100%;
  position: absolute;
  bottom:2%;
  left:0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 50px;
  -ms-flex-align: center;
      align-items: center;
}
.nav-item{
  padding:0 10px;
  font-size: 20px;
  color:blue;
  font-weight: bold;
}
.nav-item:hover{
  cursor: pointer;
}
.nav-item a{
  font-weight: bold;
  color:blue;
}
.full-content-page {
  width:60rem;
  margin:0 auto;
  background: black;
}
.highscore-list{
  background: rgb(255, 250, 96);
  color:black;
  font-weight: bold;
}
.highscore-items{
  font-size: 20px;
  padding:10px;
}
.highscore-itembox{
  width: 100%;
}
.score{
  text-align: center;
}
.highscore-title{
  position: inherit;
  font-size: 35px;
  text-align: center;
  color: red;
  text-decoration: underline;
}


